@charset "UTF-8";
.checkbox {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6); }
  .checkbox > input {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute; }
    .checkbox > input:checked + label:before {
      font-size: 16px;
      background-color: #00a2d3;
      border: none;
      color: #fff; }
  .checkbox > label {
    font-size: 0;
    cursor: pointer;
    display: block;
    min-height: 20px;
    padding: 0 0 0 25px;
    position: relative; }
    .checkbox > label:before {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #fff;
      box-sizing: border-box;
      content: '✔';
      height: 20px;
      width: 20px;
      left: 0;
      top: 50%;
      position: absolute;
      transform: translateY(-50%); }



